.dialogs {
    background-color: green;
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialog {
    margin-bottom: 10px;
    cursor: pointer;
}

.dialogsItems {
    padding: 10px;
}

.dialogsItems .active {
    color: gold;
}
.messages {
    padding: 10px;
    color: white;
}

.message {
    margin-bottom: 10px;
}

.btn {
    height: 50px;
}